<template>
<div>
    <el-dialog title="新建客户" :visible.sync="value" width="660px" append-to-body :before-close="dialogClose" class="detail-modal">
        <div class="customer-detail">
            <el-form :model="detailForm" ref="detailForm" :rules="detailFormRule" size="small" label-width="110px">
                <el-form-item label="客户名称：" prop="customer_name">
                    <el-input v-model="detailForm.customer_name"></el-input>
                </el-form-item>
                <el-form-item label="社会信用代码：" prop="social_credit_code">
                    <el-input v-model="detailForm.social_credit_code"></el-input>
                </el-form-item>
                <el-form-item label="客户级别：" prop="customer_level">
                    <el-input v-model="detailForm.customer_level"></el-input>
                </el-form-item>
                <div class="box-flex">
                    <el-form-item label="行业类型：" prop="industry_type_id" class="mr15">
                        <el-select v-model="detailForm.industry_type_id" class="w180">
                            <!-- <el-option v-for="(item,index) in industryType" :key="index" :value="item.value" :item="item.value"></el-option> -->
                        </el-select>
                    </el-form-item>
                    <el-form-item class="flex-one" prop="industry_subtype_id" label-width="0">
                        <el-select v-model="detailForm.industry_subtype_id" class="wp100">
                            <!-- <el-option v-for="(item,index) in industryType" :key="index" :value="item.value" :item="item.value"></el-option> -->
                        </el-select>
                    </el-form-item>
                </div>
                <div class="block-title">
                    联系人
                </div>
                <div v-for="(item,index) in detailForm.linkmen" :key="index">
                    <div class="box-flex">
                        <el-form-item :label="item.is_primary?'主要联系人：':'联系人'" :prop="'linkmen.'+index+'.linkman_name'" :rules="detailFormRule.linkmen[index].linkman_name">
                            <el-input v-model="item.linkman_name" class="w150 mr15" placeholder="姓名"></el-input>
                        </el-form-item>
                        <el-form-item label-width="0px" :prop="'linkmen.'+index+'.linkman_contact'" :class="['wp100',{'pr20':item.is_primary}]" :rules="detailFormRule.linkmen[index].linkman_contact">
                            <el-input v-model="item.linkman_contact" placeholder="联系方式"></el-input>
                        </el-form-item >
                        <i class="el-icon-circle-close pt10 ml10 fc-red" @click="delContactor(item,index)" v-if="!item.is_primary"></i>
                    </div>
                    <el-form-item label="职务：" :prop="'linkmen.'+index+'.linkman_post'" :rules="detailFormRule.linkmen[index].linkman_post" class="pr20">
                        <el-input v-model="item.linkman_post" placeholder="职务"></el-input>
                    </el-form-item>
                </div>
                <el-form-item>
                    <el-button plain type="primary" @click="addContactor">添加联系人</el-button>
                </el-form-item>
                <div class="block-title">
                    详细信息
                </div>
                <el-form-item label="省市：">
                    <!-- <el-cascader v-model="detailForm.customer_name"></el-cascader> -->
                </el-form-item>
                <el-form-item label="详细地址：" prop="address">
                    <el-input v-model="detailForm.address"></el-input>
                </el-form-item>
                <el-form-item label="公司网址：" prop="website">
                    <el-input v-model="detailForm.website"></el-input>
                </el-form-item>
                <el-form-item label="营业额：(元)" prop="turnover">
                    <el-input v-model="detailForm.turnover"></el-input>
                </el-form-item>
                <el-form-item label="员工数：" prop="staff_num">
                    <el-input v-model="detailForm.staff_num"></el-input>
                </el-form-item>
                <el-form-item label="主营业务：" prop="primary_business">
                    <el-input v-model="detailForm.primary_business"></el-input>
                </el-form-item>
                <el-form-item label="营业执照：" prop="business_licence_img">
                    <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove" multiple :limit="3" :on-exceed="handleExceed" :file-list="fileList">
                        <el-button size="small" type="primary">选择文件</el-button>
                        <div slot="tip" class="el-upload__tip">请提供证件的原件照片或彩色扫描件，文字/盖章清晰可辨认，jpg、jpeg、pngges ，不超过10MB</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="附件：">
                    <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove" multiple :limit="3" :on-exceed="handleExceed" :file-list="fileList">
                        <el-button size="small" type="primary">选择文件</el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="客户概述：" prop="customer_summary">
                    <el-input type="textarea" v-model="detailForm.customer_summary"></el-input>
                </el-form-item>
            </el-form>
        </div>
        <div slot="footer" class="dialog-footer tc">
            <el-button @click="dialogClose">取 消</el-button>
            <el-button type="primary" @click="confirm">确 定</el-button>
        </div>
    </el-dialog>
    </div>
</template>

<script>
import rule from "@/utils/validate-rule";
import { contactRule, contactData ,configData,configRule} from "./detailConfig";
import apis from "@/api";
export default {
  data() {
    return {
      detailForm:{},
      detailFormRule: configRule,
      fileList: []
    };
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    customerDetail: {
      type: Object,
      default: ()=>{
          return {}
      }
    }
  },
  mounted() {},
  watch: {
    value: function(nv, ov) {
        let vm = this
        vm.detailForm = Object.assign({},vm.customerDetail)
      if (nv) {
        if (vm.customerDetail&&vm.customerDetail.id) {
          vm.editContactForm()
        }else{
            this.initContactForm();
        }
      }else{
          this.resetForm()
      }
    }
  },
  methods: {
    dialogClose() {
      this.dialogVisible = false;
      this.$emit("input", false);
    },
    async confirm() {
        this.$refs.detailForm.validate((valid)=>{
            if(valid){
                let params = Object.assign({},this.detailForm)
                if(this.detailForm.id){
                    apis.customer.putEditCustomer(params).then(()=>{
                        this.dialogClose();
                        this.$emit("reload-list");
                    })
                }else{
                    apis.customer.postNewCustomer(params).then(()=>{
                        this.dialogClose();
                        this.$emit("reload-list");
                    })
                }
            }else{
                return false
            }
        })
       
    },
    save() {},
    //添加联系人
    addContactor() {
      let vm = this;
      this.detailForm.linkmen.push(
            Object.assign({}, contactData, {
              is_primary:0
            })
          );
          this.detailFormRule.linkmen.push(contactRule);
    },
    //删除联系人
    delContactor(item,index){
        if(item.is_primary!=1){
            this.detailForm.linkmen.splice(index, 1)
            this.detailFormRule.linkmen.splice(index, 1)
        }else{
            this.$message({
                message: '主联系人不可删除',
                type: 'warning'
            });
        }
    },
    editContactForm(){
        let len = this.detailForm.linkmen?this.detailForm.linkmen.length:0
        if(len==1){
            return
        }
        if(len>=2){
            for (let i = 0; i < len-1;i++) {
                this.detailFormRule.linkmen.push(contactRule)
            }
            return
        }
        if(len==0){
            this.initContactForm()
            return
        }
    },
    initContactForm() {
        this.detailFormRule.linkmen=[]
        console.log(this.detailFormRule.linkmen.length)
        this.detailForm.linkmen=[]
        for (let i = 0; i < 2; i++) {
          let is_primary = i == 0 ? 1 : 0;
          this.detailForm.linkmen.push(
            Object.assign({}, contactData, {
              is_primary
            })
          );
          this.detailFormRule.linkmen.push(contactRule);
        }
    },
    resetForm(){
        // this.detailForm=configData
        console.log(configRule)
        this.detailFormRule = configRule
        this.$emit('reset')
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 10 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    }
  }
};
</script>

<style lang="scss">
.detail-modal {
  .el-dialog__body {
    padding: 30px 40px;
  }
  .el-dialog__header {
    border-bottom: 1px solid #999;
  }
  .block-title {
    font-size: 16px;
    line-height: 2;
    font-weight: bold;
    border-bottom: 1px solid #999;
    margin-bottom: 15px;
  }
}
</style>


